<template>
  <div class="index">
    <index-header></index-header>
    <index-content></index-content>
    <div class="convenientFn">
      <order-amount class="amount"></order-amount>
      <shortcut-key class="shortcut"></shortcut-key>
    </div>
    <index-footer></index-footer>
  </div>
</template>

<script>
    import indexHeader from './components/header'
    import indexContent from "./components/content"
    import indexFooter from './components/footer'
    import orderAmount from "./components/orderAmount"
    import shortcutKey from "./components/shortcutKey"

    export default {
        name: "index",
        components: {indexHeader, indexContent, indexFooter, orderAmount, shortcutKey}
    }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";

  .index {
    padding: 0px 20px;
    background-color: $contentBg;
  }

  .convenientFn{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    .amount{
      flex:1;
      margin-right: 10px;
    }
    .shortcut{}
  }

</style>



