<template>
  <el-row class="index-header" :gutter="20">
    <el-col class="goods-num" :span="7">
      <el-autocomplete
        v-model="goodsNum"
        :fetch-suggestions="querySearchAsync"
        placeholder="扫码或手动输入商品编码/商品名"
        class="goods-input"
      >
      </el-autocomplete>
      <el-button type="primary" class="btn">添加</el-button>
    </el-col>
    <el-col class="phone-num" :span="5">
      <el-input v-model="phone" placeholder="输入会员手机号" class="phone-input"></el-input>
      <el-button type="primary" class="btn">验证</el-button>
    </el-col>
    <el-col :span="12">
     <div class="member">
       <i class="member-name">{{info.name}}</i>
       <el-tag effect="dark" type="warning">{{info.level}}</el-tag>
       <el-tag effect="plain" type="">{{info.active}}</el-tag>
       <el-tag effect="dark" type="info">黑名单</el-tag>
       <i class="member-phone">手机号：<em>{{info.phone}}</em></i>
       <i class="member-integral">积分：<em>{{info.integral}}</em></i>
     </div>
    </el-col>
  </el-row>
</template>

<script>
    export default {
        name: "index-header",
        data() {
            return {
                goodsNum: '',
                phone: '',
                restaurants: [],
                timeout: '',
                info: {
                    name: '爱新觉罗弘历',
                    level: '金卡',
                    active: '活跃',
                    blacklist: [],
                    phone: '13232324422',
                    integral: '123'
                }
            }
        },
        created() {
            this.restaurants = this.loadAll()
        },
        methods: {
            loadAll() {
                return [
                    {
                        num: '12312321',
                        value: '果子'
                    },
                    {
                        num: '123321312',
                        value: '果汁'
                    },
                    {
                        num: 'a1231132132',
                        value: '桃子'
                    },
                    {
                        num: 'bn231313',
                        value: '苹果'
                    },
                    {
                        num: 'c51665',
                        value: '香蕉'
                    },
                    {
                        num: 'r2152655',
                        value: '椰子'
                    }
                ]
            },
            querySearchAsync(str, callback) {
                let restaurants = this.restaurants
                let results = str ? restaurants.filter(this.createStateFilter(str)) : restaurants
                clearTimeout(this.timeout)
                this.timeout = setTimeout(() => {
                    callback(results)
                }, 3000 * Math.random())
            },
            createStateFilter(str) {
                return (state) => {
                    return state.num.toLowerCase().indexOf(str.toLowerCase()) !== -1 || state.value.toLowerCase().indexOf(str.toLowerCase()) !== -1
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";

  .index-header {
    padding: 10px 0;
    height: 60px;
    background-color: $contentBg;

    /deep/ {
      .el-autocomplete, .el-input {
        height: 100%;
        line-height: 100%;
        background-color: #ffffff;

        input {
          height: 100%;
          line-height: 100%;
          border-radius: 0;
        }
      }
    }

    .goods-num {
      display: flex;
      height: 100%;
      line-height: 40px;

      .goods-input {
        flex: 8;
      }

      .btn {
        flex: 2;
      }
    }

    .phone-num {
      display: flex;
      height: 100%;
      line-height: 40px;
      .phone-input {
        flex: 7;
      }

      .btn {
        flex: 3;
      }
    }

    .member {
      display: flex;
      height: 100%;
      line-height: 40px;
      align-items: center;
      justify-content: space-between;
      background-color: #ffffff;
      padding: 0 10px;
      .member-name{
        font-weight: bold;
      }
      /deep/{
        .el-tag{
          font-size: 14px;
          padding-left: 20px;
          padding-right: 20px;
          border-radius: 15px;
        }
      }
      .member-phone, .member-integral{
        font-size: 12px;
      }
    }

    .btn {
      border-radius: 0;
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px;
    }
  }
</style>



