<template>
  <div class="shortcutKey">
    <div class="usualKeys" :class="{marginRight: isExpand}">
      <div class="item" v-for="(item) in keys" :key="item.key" @click="quickKey(item.key)">
        <span>{{item.name}}</span>
        <span>【{{item.key}}】</span>
      </div>
    </div>
    <div class="expendKeys" v-if="isExpand">
      <div class="item" v-for="(item) in anotherKeys" :key="item.key" @click="quickKey(item.key)">
        <span>{{item.name}}</span>
        <span>【{{item.key}}】</span>
      </div>
    </div>
    <span class="tag" @click="changeExpand">
      <svg-icon :icon-class="isExpand ? 'packup' : 'expand'"></svg-icon>
    </span>
    <el-dialog :visible.sync="dialogTableVisible">
          <!-- 弹窗内容开始 搭配codeA和codeA的值选择展示哪种弹框 -->
          <!-- 弹窗内容结束 -->
    </el-dialog>
  </div>
</template>

<script>
    export default {
        name: "shortcutKey",
        data() {
            return {
                keys: [
                    //112
                    {
                        name: '删单',
                        key: 'F1'
                    },
                    {
                        name: '挂单',
                        key: 'F2'
                    },
                    {
                        name: '提取挂单',
                        key: 'F3'
                    },
                    {
                        name: '原单退货',
                        key: 'F4'
                    },
                    {
                        name: '办理会员',
                        key: 'F5'
                    },
                    {
                        name: '积分兑换',
                        key: 'F6'
                    },
                    {
                        name: '重打小票',
                        key: 'F7'
                    },
                    {
                        name: '药方调配',
                        key: 'F8'
                    },
                    {
                        name: '对账',
                        key: 'F9'
                    },
                    {
                        name: '交班',
                        key: 'F10'
                    },
                    {
                        name: '选择优惠券',
                        key: 'F11'
                    },
                    {
                        name: '计算器',
                        key: 'F12'
                    },
                    {
                        name: '处方记录',
                        key: 'Q'
                    },
                    {
                        name: '含麻记录',
                        key: 'W'
                    },
                    {
                        name: '药方记录',
                        key: 'E'
                    }
                ],
                anotherKeys: [
                    {
                        name: '系统设置',
                        key: 'R'
                    }
                ],
                isExpand: false,
                dialogTableVisible: false,
                codeA: 0,
                codeB: 0
            }
        },
        watch:{
            dialogTableVisible(val){
                if(val){
                    window.removeEventListener('keydown',this.focusKeydown, false)
                    window.removeEventListener('keyup',this.blurKeyup, false)
                }else{
                    window.addEventListener('keydown',this.focusKeydown, false)
                    window.addEventListener('keyup',this.blurKeyup, false)
                }
            }
        },
        created(){
            window.addEventListener('keydown',this.focusKeydown, false)
            window.addEventListener('keyup',this.blurKeyup, false)
        },
        methods: {
            quickKey() {
                this.changeDialogStatus()
            },
            changeExpand() {
                this.isExpand = !this.isExpand
            },
            changeDialogStatus(){
                this.dialogTableVisible = !this.dialogTableVisible
            },
            focusKeydown(e){
                let key = window.event.keyCode;
                e.cancelBubble = true;
                e.returnValue = false;
                if(e.preventDefault) e.preventDefault();
                if(e.stopPropagation) e.stopPropagation();
                if(key === 18) {
                    this.codeA = key
                }else{
                    this.codeB = key
                }
                if(this.codeA && this.codeB){
                    this.dialogTableVisible = true
                }
            },
            blurKeyup(e){
                let key = window.event.keyCode;
                if(key === 18){
                    this.codeA = 0
                }else{
                    this.codeB = 0
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";

  .shortcutKey {
    display: flex;
    /*padding: 0 10px;*/
    position: relative;
    background-color: #ffffff;

    span {
      display: block;
    }

    .usualKeys, .expendKeys {
      display: flex;
      flex-wrap: wrap;
      max-width: 580px;

      .item {
        cursor: pointer;
        color: $menuText;
        width: 115px;
        padding: 0 15px;
        text-align: center;
        line-height: 30px;
        max-height: 60px;

        &:hover {
          border-radius: 10px;
          background-color: $menuActiveText;
          color: #ffffff;
        }
      }
    }

    .usualKeys {
      &.marginRight {
        margin-right: 10px;
      }
    }

    .expendKeys {
      flex-direction: column;
      max-height: 180px;
      flex-wrap: wrap;
    }

    .tag {
      -moz-user-select: none;
      -webkit-user-select: none;
      user-select: none;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      padding: 6px 5px 6px 15px;
    }
  }
</style>



