<template>
  <div class="orderAmount">
    <p class="description">订单共<i>{{totalKinds}}</i>种商品，共<i>{{totalUnit}}</i>件</p>
    <div class="total">
      <div class="money">
        <p class="discounts">
          <i>会员价优惠：<em>{{memberDiscount}}</em>元</i>
          <i>满减优惠：<em>{{fullDiscount}}</em>元</i>
        </p>
        <p class="count">
          <i>总额：<em>￥{{totalAmount}}</em></i>
          <i>优惠合计：<em>￥{{totalDiscount}}</em></i>
          <i>实收：<em>￥{{paidIn}}</em></i>
        </p>
      </div>
      <el-button type="primary" class="invoice">结账</el-button>
    </div>
  </div>
</template>

<script>
    export default {
        name: "orderAmount",
        data() {
            return {
                memberDiscount: 30,
                fullDiscount: 20,
                totalDiscount: 0,
                totalAmount: 238.80,
                totalKinds: 4,
                totalUnit: 12,
                paidIn: 0
            }
        },
        created() {
            this.totalDiscount = (+this.memberDiscount + +this.fullDiscount).toFixed(2)
            this.paidIn = this.totalAmount - this.totalDiscount
        }
    }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";

  .orderAmount {
    padding: 20px 20px;
    background-color: #ffffff;

    .description {
      color: $menuText;
      line-height: 60px;

      i {
        color: $menuActiveText;
      }
    }

    .total {
      display: flex;
      justify-content: space-between;

      .money {
        .discounts {
          color: $menuText;
          font-size: 12px;
          line-height: 30px;
        }

        .count {
          line-height: 40px;
          font-size: 16px;

          em {
            color: #f10215;
          }
        }
      }

      .invoice {
        margin-left: 50px;
        width: 150px;
        height: 60px;
        border-radius: 5px;
      }
    }
  }
</style>



