<template>
  <div class="index-content">
    <el-table
      :data="tableData"
      class="goods-table"
      tooltip-effect="dark"
      fit
      highlight-current-row
      @current-change="handleCurrentChange"
    >
      <el-table-column label="商品" width="250px">
        <template slot-scope="scope">
          <p class="goods-name">{{scope.row.name}}/{{scope.row.num}}</p>
          <i class="size">{{scope.row.size}}/{{scope.row.company}}</i>
        </template>
      </el-table-column>
      <el-table-column label="批号/效期至" width="120px">
        <template slot-scope="scope">
          <p>{{scope.row.batchNum}}</p>
          <p>{{scope.row.usageDate}}</p>
        </template>
      </el-table-column>
      <el-table-column label="库存" prop="inventory"></el-table-column>
      <el-table-column label="数量" width="100px">
        <template slot-scope="scope">
          <el-input-number
            class="number-input"
            v-model="scope.row.amount"
            size="mini"
            :min="0"
            @change="getChooseChange(scope.row, scope.$index)"
          >
          </el-input-number>
        </template>
      </el-table-column>
      <el-table-column label="标价" >
        <template slot-scope="scope">
          <p class="salePrice" v-if="scope.row.salePrice">{{scope.row.salePrice}}</p>
          <p>{{scope.row.tagPrice}}</p>
        </template>
      </el-table-column>
      <el-table-column label="实价" width="100px">
        <template slot-scope="scope">
          <el-input-number
            :controls="false"
            class="number-input"
            v-model="scope.row.actualPrice"
            :min="0"
            size="mini"
            @change="getChooseChange(scope.row, scope.$index)"
          >
          </el-input-number>
        </template>
      </el-table-column>
      <el-table-column label="小计">
        <template slot-scope="scope">
          <i>{{(+scope.row.actualPrice * +scope.row.amount).toFixed(2)}}</i>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
            <span class="goods-delete" @click="handleDelete(scope.$index)">
              <svg-icon icon-class="delete"/>
            </span>
        </template>
      </el-table-column>
    </el-table>
    <div class="promotion">
      <div class="medicine-img">
        <i class="tip">处方</i>
        <img :src="promoteInfo.url" :alt="promoteInfo.name" class="img">
      </div>
      <div class="usage">
        <div class="frequency">
          <p>使用频次</p>
          <i>一日三次</i>
        </div>
        <div class="time">
          <p>使用时间</p>
          <i>餐后</i>
        </div>
        <div class="date">
          <p>有效期至</p>
          <i>2022.06</i>
        </div>
      </div>
      <div class="notices">
        <p>注意事项与禁忌：</p>
        <i>服药期间不能饮酒，连续服用3个月效果更佳；连续服用打打打打打打哇对哇对哇对哇对哇对哇对哇撒打发无法</i>
      </div>
      <div class="sales">
        <p class="title">促销信息</p>
        <div class="sales-card">
          <div class="promote-card">
            <p class="coupons">单笔买<i>30</i>元送中秋优惠券</p>
            <p>全部商品可用</p>
            <p>订单满300元减20元</p>
            <p>促销周期：<i>每月1号~5号</i></p>
            <p>促销周期：<i>2019.08.31 ~ 2019.12.31</i></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "index-content",
        data() {
            return {
                promoteInfo: {
                    name: '仁和_阿斯匹林肠溶片',
                    url: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
                    frequency: '3',
                    time: '餐后',
                    date: '2022.06',
                    buyAmount: '30',
                    gift: '中秋优惠券',
                    application: '全部',
                    fullReduction: '300元减20元',
                    salesDate: '每月1~5号',
                    salesPeriod: '2019.08.31'
                },
                tableData: [
                    {
                        id: '123',
                        name: '阿司匹林',
                        num: '010101213456',
                        size: '50mg*25片*3板/盒',
                        company: '湖南九芝堂',
                        batchNum: '1821521',
                        usageDate: '2023-12-12',
                        inventory: '200',
                        amount: 1,
                        tagPrice: '19.90',
                        salePrice: '19.90',
                        actualPrice: 19.90,
                        total: ''
                    },
                    {
                        id: '123',
                        name: '阿司匹林',
                        num: '010101213456',
                        size: '50mg*25片*3板/盒',
                        company: '湖南九芝堂',
                        batchNum: '1821521',
                        usageDate: '2023-12-12',
                        inventory: '200',
                        amount: 1,
                        tagPrice: '19.90',
                        salePrice: '',
                        actualPrice: 19.90,
                        total: ''
                    },
                    {
                        id: '123',
                        name: '阿司匹林',
                        num: '010101213456',
                        size: '50mg*25片*3板/盒',
                        company: '湖南九芝堂',
                        batchNum: '1821521',
                        usageDate: '2023-12-12',
                        inventory: '200',
                        amount: 1,
                        tagPrice: '19.90',
                        salePrice: '19.90',
                        actualPrice: 19.90,
                        total: ''
                    },
                    {
                        id: '123',
                        name: '阿司匹林',
                        num: '010101213456',
                        size: '50mg*25片*3板/盒',
                        company: '湖南九芝堂',
                        batchNum: '1821521',
                        usageDate: '2023-12-12',
                        inventory: '200',
                        amount: 1,
                        tagPrice: '19.90',
                        salePrice: '19.90',
                        actualPrice: 19.90,
                        total: ''
                    },
                    {
                        id: '123',
                        name: '阿司匹林',
                        num: '010101213456',
                        size: '50mg*25片*3板/盒',
                        company: '湖南九芝堂',
                        batchNum: '1821521',
                        usageDate: '2023-12-12',
                        inventory: '200',
                        amount: 1,
                        tagPrice: '19.90',
                        salePrice: '19.90',
                        actualPrice: 19.90,
                        total: ''
                    }
                ],
                chooseId: ''
            }
        },
        methods: {
            getChooseChange(row, index) {
                console.log(row.amount, 'row.amount')
                this.tableData[index] = row
            },
            handleDelete(index) {
                this.$confirm('是否删除当前项目?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.tableData.splice(index, 1)
                }).catch((e) => {
                    console.error(e);
                });

            },
            handleCurrentChange(val){
              this.chooseId = val.id
            }
        }
    }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";
  .index-content {
    display: flex;
    height: calc( 100vh - 300px);
    .goods-table {
      flex:8;
      margin-right: 10px;
      .goods-delete {
        cursor: pointer;
        width: 10px;
        height: 10px;
      }
      .salePrice{
        color: $menuText;
        text-decoration: line-through;
        font-size: 12px;
        line-height: 16px;
        margin: 0 0;
      }
      .number-input{
        width: 80px;
      }
    }

    .promotion {
      flex:2;
      padding: 0 10px;
      height: 100%;
      overflow-y: auto;
      background-color: #ffffff;

      .medicine-img {
        position: relative;
        width: 100%;
        height: 140px;
        text-align: center;
        padding: 20px 20px;

        .img {
          height: 100%;
          width: 200px;
        }

        .tip {
          position: absolute;
          top: 0;
          right: 0;
          padding: 4px 6px;
          background-color: red;
          color: #ffffff;
          border-radius: 50%;
        }

      }

      .usage {
        display: flex;
        justify-content: space-between;
        line-height: 30px;

        p {
          font-weight: bold;
        }
      }

      .notices {
        p {
          line-height: 30px;
          font-weight: bold;
        }

        i {
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
          line-height: 20px;
        }
      }

      .sales {
        .title {
          line-height: 40px;
          font-weight: bold;
        }

        .promote-card {
          cursor: pointer;
          padding: 10px 10px;
          padding-top: 0;
          background-color: palegreen;
          color: $menuText;

          .coupons {
            line-height: 30px;
          }
        }
      }
    }
  }
</style>



