<template>
  <div class="index-footer">
    <i>药师帮后台ERP系统 <em>V1.0</em></i>
    <i>@广州速道易科技有限公司版权所有</i>
    <i>九章医药连锁股份有限公司江南西店</i>
    <i>款台号：<em>01</em>号</i>
    <i>当前小票号: <em>214123</em></i>
    <i>联网状态：<em>{{isOnline ? '已联网' : '已断线'}}</em></i>
    <i>小票打印机：<em>未连接</em></i>
    <i>{{date}}</i>
    <i>当前操作人：<em>祥子</em></i>
  </div>
</template>

<script>
  import {formatTime} from '@/utils/validate'
    export default {
        name: "index-footer",
        data() {
            return {
                isOnline: false,
                timer: '',
                date: formatTime(new Date())
            }
        },
        mounted() {
            clearInterval(this.timer)
            this.timer = setInterval(() => {
                this.isOnline = navigator.onLine
                this.date = formatTime(new Date())
            }, 1000)
        },
        methods: {}
    }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";

  .index-footer {
    margin-top: 10px;
    font-size: 12px;
    line-height: 40px;
    color: $menuText;

    i {
      margin-right: 10px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
</style>



